module.exports = ".form-bg {\r\n    background: #e4e6e6;\r\n}\r\n\r\n.form-bg {\r\n    font-family: 'Kanit', sans-serif !important;\r\n}\r\n\r\n.form-horizontal .header {\r\n    background: #3f9cb5;\r\n    padding: 13px 25px;\r\n    font-size: 30px;\r\n    color: #fff;\r\n    text-align: center;\r\n    text-transform: uppercase;\r\n    border-radius: 3px 3px 0 0;\r\n}\r\n\r\n.form-horizontal .heading {\r\n    font-size: 16px;\r\n    color: #3f9cb5;\r\n    margin: 10px 0 20px 0;\r\n    text-transform: capitalize;\r\n}\r\n\r\n.form-horizontal .form-content {\r\n    padding: 25px;\r\n    background: #fff;\r\n}\r\n\r\n.form-horizontal .form-control {\r\n    padding: 12px 16px 12px 39px;\r\n    height: 50px;\r\n    font-size: 14px;\r\n    color: #2b2a2a;\r\n    border: none;\r\n    border-bottom: 2px solid #ccc;\r\n    border-radius: 0;\r\n    box-shadow: none;\r\n    margin-bottom: 15px;\r\n}\r\n\r\n.form-horizontal .form-control:focus {\r\n    border-color: #3f9cb5;\r\n    box-shadow: none;\r\n}\r\n\r\n.form-horizontal .control-label {\r\n    font-size: 17px;\r\n    color: #ccc;\r\n    position: absolute;\r\n    top: 5px;\r\n    /* left: 27px; */\r\n    text-align: center;\r\n}\r\n\r\n.form-horizontal textarea.form-control {\r\n    resize: vertical;\r\n    height: 130px;\r\n}\r\n\r\n.form-horizontal .btn {\r\n    font-size: 18px;\r\n    color: #4e4e4e;\r\n    float: right;\r\n    margin: 10px 0;\r\n    border: 2px solid #ccc;\r\n    border-radius: 0;\r\n    padding: 10px 25px;\r\n    transition: all 0.5s ease 0s;\r\n}\r\n\r\n.form-horizontal .btn:hover {\r\n    background: #fff;\r\n    border-color: #3f9cb5;\r\n}\r\n\r\n/* .btn {\r\n    text-transform: uppercase;\r\n    letter-spacing: 1px;\r\n    color: #fe6d6d;\r\n    border-radius: 0;\r\n    position: relative;\r\n}\r\n\r\n.btn:hover {\r\n    color: #fe6d6d;\r\n}\r\n\r\n.btn:after {\r\n    content: \"\";\r\n    width: 100%;\r\n    height: 100%;\r\n    border: 2px solid #fe6d6d;\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    transition: width 0.2s ease 0s, height 0.2s ease 0.1s, border-top-width 0.1s ease 0.2s, margin 0.2s ease 0s;\r\n}\r\n\r\n.btn:hover:after {\r\n    width: 60%;\r\n    height: 0;\r\n    border-width: 0 2px 2px;\r\n    margin: 0 20%;\r\n    transition: width 0.2s ease 0.2s, height 0.2s ease 0.1s, border-top-width 0.1s ease 0s, margin 0.2s ease 0.2s;\r\n}\r\n\r\n.btn.blue,\r\n.btn.blue:hover {\r\n    color: #22ace3;\r\n}\r\n\r\n.btn.blue:after {\r\n    border-color: #22ace3;\r\n}\r\n\r\n.btn.orange,\r\n.btn.orange:hover {\r\n    color: #f56f17;\r\n}\r\n\r\n.btn.orange:after {\r\n    border-color: #f56f17;\r\n}\r\n\r\n.btn.green,\r\n.btn.green:hover {\r\n    color: #47cf77;\r\n}\r\n\r\n.btn.green:after {\r\n    border-color: #47cf77;\r\n} */\r\n\r\n@media only screen and (max-width: 767px) {\r\n    .btn {\r\n        margin-bottom: 20px;\r\n    }\r\n}\r\n\r\n/* Redio button  */\r\n\r\n.grad {\r\n    background-image: linear-gradient(to bottom right, #217d96, #217d96);\r\n}\r\n\r\n.circle {\r\n    /* ชื่อคลาสต้องตรงกับ <img class=\"circle\"... */\r\n    height: auto;\r\n    /* ความสูงปรับให้เป็นออโต้ */\r\n    width: auto;\r\n    /* ความสูงปรับให้เป็นออโต้ */\r\n    border-radius: 50%;\r\n    /* ปรับเป็น 50% คือความโค้งของเส้นขอบ*/\r\n    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);\r\n    /* เงาของรูป */\r\n}\r\n\r\n/* rating */\r\n\r\n.container {\r\n    display: block;\r\n    position: relative;\r\n    padding-left: 35px;\r\n    margin-bottom: 12px;\r\n    cursor: pointer;\r\n    font-size: 22px;\r\n    -webkit-user-select: none;\r\n    -moz-user-select: none;\r\n    -ms-user-select: none;\r\n    user-select: none;\r\n}\r\n\r\n/* Hide the browser's default radio button */\r\n\r\n.container input {\r\n    position: absolute;\r\n    opacity: 0;\r\n    cursor: pointer;\r\n}\r\n\r\n/* Create a custom radio button */\r\n\r\n.checkmark {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    height: 25px;\r\n    width: 25px;\r\n    background-color: #eee;\r\n    border-radius: 50%;\r\n}\r\n\r\n/* On mouse-over, add a grey background color */\r\n\r\n.container:hover input~.checkmark {\r\n    background-color: #ccc;\r\n}\r\n\r\n/* When the radio button is checked, add a blue background */\r\n\r\n.container input:checked~.checkmark {\r\n    background-color: #2196F3;\r\n}\r\n\r\n/* Create the indicator (the dot/circle - hidden when not checked) */\r\n\r\n.checkmark:after {\r\n    content: \"\";\r\n    position: absolute;\r\n    display: none;\r\n}\r\n\r\n/* Show the indicator (dot/circle) when checked */\r\n\r\n.container input:checked~.checkmark:after {\r\n    display: block;\r\n}\r\n\r\n/* Style the indicator (dot/circle) */\r\n\r\n.container .checkmark:after {\r\n    top: 9px;\r\n    left: 9px;\r\n    width: 8px;\r\n    height: 8px;\r\n    border-radius: 50%;\r\n    background: white;\r\n}\r\n\r\n/* uploadfile  */\r\n\r\n::-webkit-file-upload-button {\r\n    background-color: #217d96;\r\n    border: none;\r\n    color: white;\r\n    padding: 10px 22px;\r\n    text-align: center;\r\n    font-size: 20px;\r\n    margin: 2px 0px;\r\n    opacity: 0.6;\r\n    transition: 0.3s;\r\n    display: inline-block;\r\n    text-decoration: none;\r\n    cursor: pointer;\r\n    border-radius: 6px;\r\n}\r\n\r\n::-webkit-file-upload-button:hover {\r\n    opacity: 1\r\n}\r\n\r\n/* button */\r\n\r\n.button {\r\n    display: inline-block;\r\n    border-radius: 4px;\r\n    background-color: #217d96;\r\n    border: none;\r\n    color: #FFFFFF;\r\n    text-align: center;\r\n    font-size: 18px;\r\n    padding: 20px;\r\n    width: 100%;\r\n    transition: all 0.5s;\r\n    cursor: pointer;\r\n    margin: 5px;\r\n}\r\n\r\n.button span {\r\n    cursor: pointer;\r\n    display: inline-block;\r\n    position: relative;\r\n    transition: 0.5s;\r\n}\r\n\r\n.button span:after {\r\n    content: '\\00bb';\r\n    position: absolute;\r\n    opacity: 0;\r\n    top: 0;\r\n    right: -20px;\r\n    transition: 0.5s;\r\n}\r\n\r\n.button:hover span {\r\n    padding-right: 25px;\r\n}\r\n\r\n.button:hover span:after {\r\n    opacity: 1;\r\n    right: 0;\r\n}"